import React, { useState, useEffect } from 'react';
import { Flex, WhiteSpace } from 'antd-mobile';

import { RequestAdminGet } from './layout_requests';
import Header from './header';

import './index.css';
import 'antd/dist/antd.css';
import 'antd-mobile/dist/antd-mobile.css';

const NoFooterSiteLayout = ({ children }) => {
  const [requestCount, setRequestCount] = useState(0);

  useEffect(() => { setRequestCount(requestCount + 1); }, []);

  return (
    <div className="flex-container">
      <Header></Header><WhiteSpace size="lg" />
      <RequestAdminGet requestCount={requestCount}></RequestAdminGet>
      <Flex>
        <Flex.Item>
          <div className="flex-main-container">
            {children}
            <WhiteSpace size="lg" />
            <WhiteSpace size="lg" />
            <WhiteSpace size="lg" />
          </div></Flex.Item>
      </Flex>
    </div>
  )
}

export default NoFooterSiteLayout;